.side{
  width: 28%;
  float: right;
  background-color: #04d9ff;
  margin: 0% 1% 0% 1%;
  margin-top: 60px;
  
}

.component{
  position: fixed;
}

.info{
  margin-top: 1em;
  background-color: #04d9ff;
}

.main{
  width: 68%;
  float: left;
  background-color: #04d9ff;
  margin: 0% 1% 0% 1%;
  margin-top: 60px;
}

img{
  width: 100%;
}

.ex1, .ex2, .ex3 {
  width: 31.3%;
  float: left;
}

.ed1, .ed2, .ed3 {
  width: 31.3%;
  float: left;
}

.ex1 {
  float: left;
}
.ex2 {
  margin: 0% 3%;
}
.ex3 {
  float: right;
  margin: 0%;
}

.ed1 {
  float: left;
}
.ed2 {
  margin: 0% 3%;
}
.ed3 {
  float: right;
  margin: 0%;
}

.logo{
  width: 60%;
  float: left;
  padding-left: 10%;
}


.pic1{
  padding-top: 25%;
  padding-bottom: 25%;

}

.pic2{
  padding-top: 16%;
}

.pic3{
  padding-top: 16%;
}

.Job1{
  padding-top: 43%;
}

.me1{
  columns: 2;
}

@media all and (max-width: 660px) {
  .me1 {
    columns: 1;
  }
}

@media all and (max-width: 400px) {
  .me1 {
    columns: 1;
  }

  .main{
    float: none;
    width: 100%;
    margin-top: 0%;
    margin-bottom: 0%;
    
  }

  .side{
    float: none;
    width: 100%;
    margin-top: 0%;
    margin-bottom: 0%;
  }

  .component{
    position: static;
    background-color: #333;
  }

  .home{
    background-color: #333;
  }
}

p, h1{
  font-family: 'Roboto Slab', serif;
}

.container1{
  margin-bottom: 9em;
}

.container2{
  margin-bottom: 2em;
}
.container3{
  margin-bottom: 2em;
}

.Job1, .Job2, .Job3{
  margin-bottom: 2em;
}

li{
  margin-bottom: 0.7em;
}

.btn{
  margin: 5% 0% 5% 0%;
}

